.mfp-iframe-holder .mfp-close, .mfp-image-holder .mfp-close {
  right: -30px;
  width: 20px;
}
@media (max-width: 1024px) {
  .mfp-iframe-holder .mfp-close, .mfp-image-holder .mfp-close {
    top: 10px !important;
  }
}
@media (max-width: 576px) {
  .mfp-iframe-holder .mfp-close, .mfp-image-holder .mfp-close {
    right: 0;
  }
}

.mfp-figure button.mfp-close:after {
  background: #fff;
}

.mfp-figure button.mfp-close:before {
  background: #fff;
