.making-order-address {
  position: relative;
  margin-bottom: 18px;
  /* для элемента input c type="radio" */
  /* для элемента label связанного с .custom-radio */
  /* создание в label псевдоэлемента  before со следующими стилями */
  /* стили при наведении курсора на радио */
  /* стили для радиокнопки, находящейся в состоянии checked */
  /* стили для радиокнопки, находящейся в состоянии disabled */
}
.making-order-address .custom-radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.making-order-address .custom-radio + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  padding-left: 24px;
}
.making-order-address .custom-radio + label::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  border: 1px solid #D3D3D3;
  border-radius: 50%;
  margin-right: 8px;
  position: absolute;
  left: 0;
  top: 0;
}
.making-order-address .custom-radio:not(:disabled):not(:checked) + label:hover::before {
  border-color: #000;
}
.making-order-address .custom-radio:checked + label::after {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  background-color: #2A4455;
  position: absolute;
  top: 4px;
  left: 4px;
  border-radius: 50%;
}
.making-order-address .custom-radio:checked + label::before {
  content: "";
  border-color: #2A4455;
}
.making-order-address .custom-radio:disabled + label::before {
  background-color: #e9ecef;
}
